<template>
    <div id="thir">
        <div class="ti">第三页</div>
        <input type="file"  @change="fileIn" name="cover" accept="image/*" capture="camera" multiple/>
        <div></div>
    </div>
</template>
<script>
export default {
    methods:{
        fileIn(e){
            var files = e.target.files;
            console.log(files.length);
            for(var i = 0 ;i < files.length;i++){
                if(!(/image/.test(files[i].type))){
                    continue;
                }
                var reader = new FileReader();
                reader.readAsDataURL(files[i]);
                reader.onload = function(){
                    var im = document.createElement("img");
                    im.src = this.result;
                    im.style = "height:20vw;width:20vw";
                    document.getElementById('thir').appendChild(im);
            }

            };
            
            
           
        }
    }
    
}
</script>
<style>
#thir{
    width: 100%;
    height: 100%;
    background-image: url("https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1548147596&di=13fc1a9d6489a1da23bc58df8f03a71b&src=http://pic.ffpic.com/files/2018/0108/1211cyegwxdljmdqtp4.jpg");
    background-size: 100vw auto;
    background-position-y:bottom; 
}
#showT{
    position:absolute;
    top:30vw;
    font-family: FangSong  ;
    font-size: 20vw;
    color: white;
}
</style>





