import Vue from 'vue'
import Router from 'vue-router'
import first from '@/components/first.vue'
import second from '@/components/second.vue'
import third from '@/components/third.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'first',
      component: first
    },
    {
      path: '/second',
      name: 'second',
      component: second
    },
    {
      path: '/third',
      name: 'third',
      component: third
    }
  ]
})



